<template>
    <div>
        <el-menu
                class="el-container headerNav"
                :style="headerNavStyle"
                default-active="1"
                mode="horizontal"
                active-text-color="#409EFF"
                ref="headerNav">
            <el-menu-item class="el-col-3 el-col-push-4" index="0" style="text-decoration: none" @click="switchPages">
                    <div style="height: 100%;width: 100%;text-decoration: none">
                        <svg width="121" height="52" xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink">
                            <g>
                                <title>background</title>
                                <rect fill="#fff" id="canvas_background" height="54" width="123" y="-1" x="-1"/>
                                <g display="none" overflow="visible" id="canvasGrid">
                                    <rect fill="url(#gridpattern)" stroke-width="0" y="0" x="0" height="100%" width="100%"/>
                                </g>
                            </g>
                            <g>
                                <title>Layer 1</title>
                                <image xlink:href="data:image/png;base64," id="svg_1" height="63.23905" width="143.00001" y="-5.0996" x="-9.00001"/>
                            </g>
                        </svg>
                    </div>
            </el-menu-item>
            <el-menu-item class="el-col-1 el-col-push-12 headerItems" index="1"
                          @click="switchPages">首页</el-menu-item>
            <el-menu-item class="el-col-1 el-col-push-12 headerItems" index="2"
                          @click="switchPages">关于
            </el-menu-item>
<!--            <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-12 headerItems" index="3"-->
<!--                          @click="loginDialogVisible = true">登录-->
<!--            </el-menu-item>-->
            <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-12 headerItems" index="3"
                          @click="switchPages">登录 | 注册
            </el-menu-item>
            <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-13 headerItems" index="4"
                          @click="$router.push({path: '/addHouses'})">添加
            </el-menu-item>
<!--            <el-menu-item v-if="!isLogin" class="el-col-1 el-col-push-12 headerItems" index="4"-->
<!--                          @click="RegisterDialogVisible = true">注册-->
<!--            </el-menu-item>-->
            <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-12 headerItems" index="4"
                          @click="$router.push({path: '/user' + '/' + userData.userPhone + '/' + userData.userName})">
                {{userData.userName}}
            </el-menu-item>
            <el-menu-item v-if="isLogin" class="el-col-1 el-col-push-12 headerItems" index="5"
                          route=""
                          @click="logout">
                退出
            </el-menu-item>
        </el-menu>
        <!--登录对话框-->
        <div>
            <el-dialog title="登录" :visible.sync="loginDialogVisible" center width="25%">
                <p style="text-align: center">
                    <el-link type="info" @click="gotoRegister('userLoginInfo')">没有账号?去注册</el-link>
                </p>
                <el-form :model="userLoginInfo" status-icon :rules="loginCheckRules" ref="userLoginInfo"
                         :label-position="labelPosition" label-width="100px">
                    <el-form-item label="手机号/用户名" prop="name">
                        <el-input v-model.trim="userLoginInfo.name"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model.trim="userLoginInfo.password"></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="loginDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="doLogin('userLoginInfo')">登 陆</el-button>
                </div>
            </el-dialog>
        </div>
        <!--注册对话框-->
        <div>
            <el-dialog title="注册" :visible.sync="RegisterDialogVisible" center width="35%">
                <p style="text-align: center">
                    <el-link type="info" @click="gotoLogin('userRegisterInfo')">已有账号?去登陆</el-link>
                </p>

                <el-form :model="userRegisterInfo" status-icon :rules="registerCheckRules" ref="userRegisterInfo"
                         :validate-on-rule-change="false"
                         label-width="100px">
                    <el-form-item label="手机号" prop="phoneNumber">
                        <el-input type="text" v-model.trim="userRegisterInfo.phoneNumber" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="验证码" prop="VerificationCode">
                        <el-input v-model.number.trim="userRegisterInfo.VerificationCode" maxlength="6" show-word-limit
                                  clearable>
                            <el-button slot="append" @click="getVerificationCode('userRegisterInfo')"
                                       :disabled="disableGetVerificationCode">
                                {{leftTime}}
                            </el-button>
                        </el-input>
                    </el-form-item>
                    <el-form-item label="用户名" prop="name">
                        <el-input type="text" v-model.trim="userRegisterInfo.name" autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="密码" prop="password">
                        <el-input type="password" v-model.trim="userRegisterInfo.password"
                                  autocomplete="off"></el-input>
                    </el-form-item>
                    <el-form-item label="确认密码" prop="checkPass">
                        <el-input type="password" v-model.trim="userRegisterInfo.checkPass"
                                  autocomplete="off"></el-input>
                    </el-form-item>
                </el-form>

                <div slot="footer" class="dialog-footer">
                    <el-button @click="RegisterDialogVisible = false">取 消</el-button>
                    <el-button type="primary" @click="doRegister('userRegisterInfo')">注 册</el-button>
                </div>
            </el-dialog>
        </div>

        <!-- 首页组件 -->
        <home v-show="homePage"/>
        <about v-show="aboutPage"/>
        <login v-show="loginPage"/>
    </div>
</template>

<script>
// 手机号码验证规则
import Home from '../components/Home'
import About from '../components/About'
import Login from '../components/login'
const validatePhoneNumber = (rule, value, callback) => {
  const that = this
  if (!value) {
    // this.disableGetVerificationCode = true
    return callback(new Error('手机号不能为空!'))
  }
  if (value.length < 11 || !/^(1[35789]\d{9})$/.test(value)) {
    // this.disableGetVerificationCode = true
    return callback(new Error('请输入正确的11位中国大陆手机号!'))
  } else {
    that.$http.post('/check/userphone', {
      userPhone: value
    }, {headers: {'Content-Type': 'application/json'}})
      .then(function (response) {
        if (response.data.errorCode === 200) {
          // that.disableGetVerificationCode = false
          callback()
        } else {
          return callback(new Error(response.data.errorMsg))
        }
      })
    // eslint-disable-next-line handle-callback-err
      .catch(function (error) {
        return callback(new Error('检测手机号是否可用错误,请稍后重试!'))
        // that.disableGetVerificationCode = false
        // callback()
      })
  }
}

export default {
  name: 'index',
  data: function () {
    const validateLoginName = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入用户名或手机号!'))
      } else {
        callback()
      }
    }
    const validateLoginPassword = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入登录密码!'))
      } else {
        callback()
      }
    }
    // 检验用户名是否已用,请求后端接口
    const validateName = (rule, value, callback) => {
      const that = this
      if (value === '') {
        return callback(new Error('请输入用户名!'))
      } else {
        setTimeout(() => {
          that.$http.post('/check/username', {
            userName: value
          }, {headers: {'Content-Type': 'application/json'}})
            .then(function (response) {
              // console.log(response)
              if (response.data.errorCode === 200) {
                // that.disableGetVerificationCode = false
                callback()
              } else {
                return callback(new Error(response.data.errorMsg))
              }
            })
          // eslint-disable-next-line handle-callback-err
            .catch(function (error) {
              return callback(new Error('检测用户名是否可用错误,请稍后重试!'))
              // that.disableGetVerificationCode = false
              // callback()
            })
        }, 500)
      }
    }
    // 密码验证规则
    const validatePassword = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入密码!'))
      } else {
        setTimeout(() => {
          if (value.length < 6 || value.search(/[a-z|A-Z]/) === -1 || value.search(/\d/) === -1 || value.search(/[@#$]/) === -1) {
            return callback(new Error('密码需六位以上,包含字母、数字及“@“、”#”、“$”中的一个'))
          }
          callback()
        }, 600)
      }
    }
    // 二次输入密码规则检验
    const validatePass2 = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请再次输入密码!'))
      } else if (value !== this.userRegisterInfo.password) {
        return callback(new Error('两次输入密码不一致!'))
      } else {
        callback()
      }
    }
    // 验证码验证规则
    const validateCOde = (rule, value, callback) => {
      if (value === '') {
        return callback(new Error('请输入六位验证码!'))
      } else if (value.toString().length !== 6) {
        return callback(new Error('请输入正确的六位验证码!'))
      } else {
        callback()
      }
    }

    return {
      homePage: true,
      aboutPage: false,
      loginPage: false,
      pageRolling: 0,
      // header 的样式,随下滑而改变
      headerNavStyle: 'height: 70px;z-index: 1;',
      // 登录弹出框,默认为不弹出
      loginDialogVisible: false,
      // 注册弹出框,默认为不弹出
      RegisterDialogVisible: false,
      // 登录、注册弹出框的 label 位置,在输入框的上方,可选值:top left(左对齐) right(右对齐)
      labelPosition: 'top',
      // 登录信息
      userLoginInfo: {
        // 用户名或手机号
        name: '',
        // 密码
        password: ''
      },
      // 注册信息
      userRegisterInfo: {
        // 手机号
        phoneNumber: '',
        // 用户名
        name: '',
        // 密码
        password: '',
        // 再次输入密码
        checkPass: '',
        // 验证码
        VerificationCode: ''
      },
      leftTime: '获取验证码',
      disableGetVerificationCode: false,
      // 登录表单检验规则
      loginCheckRules: {
        name: [
          {validator: validateLoginName, trigger: 'blur'}
        ],
        password: [
          {validator: validateLoginPassword, trigger: 'blur'}
        ]
      },
      // 注册表单检验规则
      registerCheckRules: {
        // 不需要失焦验证,改为点击获取验证码按钮时进行验证
        phoneNumber: [],

        VerificationCode: [
          {validator: validateCOde, trigger: 'blur'}
        ],
        name: [
          {validator: validateName, trigger: 'blur'}
        ],
        password: [
          {validator: validatePassword, trigger: 'blur'}
        ],
        checkPass: [
          {validator: validatePass2, trigger: 'blur'}
        ]
      },
      // 用户是否已经登录,默认为未登录
      isLogin: false,
      // 用户个人信息
      userData: {
        // 用户头像url
        userHead: '',
        // 昵称
        userName: '麦田里的风',
        // 手机号
        userPhone: '19927457507',
        // 是否学生认证
        studentAuthentication: false
      },
      loginCookie: ''
    }
  },
  watch: {
    // 监控页面滑动使 header 样式发生改变
    pageRolling (newValue, oldValue) {
      const that = this
      // console.log(newValue)
      if (newValue === 0) {
        that.headerNavStyle = '' +
                        'z-index: 1;'
      } else {
        that.headerNavStyle = '' +
                        'background: rgba(255,255,255,0.8);' +
                        'position: fixed;\n' +
                        'top: 0;\n' +
                        'width: 100%;\n' +
                        'z-index: 1;'
      }
    }
  },
  methods: {
    scrollToTop () {
      this.pageRolling = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop
    },
    switchPages (e) {
      const that = this
      switch (e.index) {
        case '0':
          this.$router.push('/')
          that.homePage = true
          that.aboutPage = false
          that.loginPage = false
          break
        case '1':
          this.$router.push('/')
          that.homePage = true
          that.aboutPage = false
          that.loginPage = false
          break
        case '2':
          this.$router.push('/about')
          that.homePage = false
          that.aboutPage = true
          that.loginPage = false
          break
        case '3':
          this.$router.push('/login')
          that.homePage = false
          that.aboutPage = false
          that.loginPage = true
          break
      }
    },
    // 获取验证码
    getVerificationCode (formName) {
      const that = this
      // 当点击获取验证码后,为手机号添加验证规则
      that.registerCheckRules.phoneNumber = [{validator: validatePhoneNumber, trigger: 'blur'}]

      // 检验手机号是否填写正确
      that.$refs[formName].validateField('phoneNumber', function (errorMessage) {
        if (errorMessage === '') {
          that.leftTime = '发送中...'
          that.$http.get('/code/' + that.userRegisterInfo.phoneNumber)
            .then(function (response) {
              if (response.data.errorCode === 200) {
                // 禁用获取验证码按钮并开始倒计时,倒计时结束后停止禁用按钮
                that.disableGetVerificationCode = true
                let leftTime = 60
                const intervalID = setInterval(() => {
                  if (leftTime > 0) {
                    that.leftTime = '还剩' + leftTime-- + '秒'
                  } else {
                    that.leftTime = '获取验证码'
                    that.disableGetVerificationCode = false
                    clearInterval(intervalID)
                  }
                }, 1000)
              }
            })
            .catch(function (error) {
              console.log(error)
            })
            .then(function () {
              // always executed
            })
        } else {
          that.registerCheckRules.phoneNumber = []
        }
      })
    },
    // 执行登录请求
    doLogin (formName) {
      const that = this
      that.$refs[formName].validate((valid, object) => {
        // console.log(object)
        if (valid) {
          // 加载动画
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            background: 'rgba(245,245,245,0.7)'
          })

          that.$http.post('/login', {
            userName: that.userLoginInfo.name,
            userPassword: that.userLoginInfo.password
          }, {headers: {'Content-Type': 'application/json'}})
            .then(function (response) {
              console.log(response)

              if (response.data.errorCode === 200) {
                // 登录成功,保存令牌到 cookie (有效期: 1 小时)
                that.$cookie.set('token', response.data.data.token, {expires: '1h'})
                // 同时用 cookie 请求用户信息
                that.$http.get('/i', {
                  params: {
                    Cookie: that.$cookie.get('token')
                  }
                })
                  .then(function (response) {
                    if (response.data.errorCode === 200) {
                      // 将请求到的数据写到用户信息中
                      that.userData = response.data.data
                      // 将用户信息保存到 cookie,一小时过期
                      that.$cookie.set('userData', response.data.data, {expires: '1h'})
                      // 结束动画
                      loading.close()
                      // 跳转到首页
                      that.$router.push('/refreshIndex')
                    }
                  })
                  .catch(function (error) {
                    console.log(error)
                  })
                  .then(function () {
                    // always executed
                  })
              } else {
                // 结束动画
                loading.close()
                alert(response.data.data.errorMessage)
              }
            })
            .catch(function (error) {
              // 结束动画
              loading.close()
              alert(error)
              // console.log(error)
            })
        }
      })
    },
    // 执行注册请求
    doRegister: function (formName) {
      const that = this
      // 当点击注册按钮后,为手机号添加验证
      that.registerCheckRules.phoneNumber = [{validator: validatePhoneNumber, trigger: 'blur'}]

      that.$refs[formName].validate((valid, object) => {
        /*
                                              * valid : 是否通过验证
                                              * object : 未通过验证的字段
                                              */
        // console.log(object)
        if (valid) {
          that.registerCheckRules.phoneNumber = []
          // 加载动画
          const loading = this.$loading({
            lock: true,
            text: 'Loading',
            background: 'rgba(245,245,245,0.7)'
          })
          // 向服务器提交请求
          that.$http.post('/register', {
            userPhone: that.userRegisterInfo.phoneNumber,
            userName: that.userRegisterInfo.name,
            userPassword: that.userRegisterInfo.password,
            code: that.userRegisterInfo.VerificationCode
          }, {headers: {'Content-Type': 'application/json'}})
            .then(function (response) {
              loading.close()
              console.log(response)
              console.log(response.status)
              console.log(response.data)

              if (response.status === 200) {
                if (response.data.errorCode === 4003 || response.data.errorCode === 4004) {
                  // 注册出现问题
                  alert(response.data.errorMessage)
                } else if (response.data.errorCode === 200) {
                  // 注册通过,向后端请求个人数据保存到本地,跳转首页

                }
              }
            })
            .catch(function (error) {
              loading.close()
              alert(error)
              // console.log(error)
            })
        } else {
          that.registerCheckRules.phoneNumber = []
          // console.log('请完善信息')
          return false
        }
      })
    },
    // 打开注册对话框,关闭登录对话框
    gotoRegister (formName) {
      this.RegisterDialogVisible = true
      this.loginDialogVisible = false
      this.$refs[formName].clearValidate()
    },
    // 打开登录对话框,关闭注册对话框
    gotoLogin (formName) {
      this.loginDialogVisible = true
      this.RegisterDialogVisible = false
      this.$refs[formName].clearValidate()
    },
    // 退出登录
    logout () {
      // 删除 token
      this.$cookie.delete('token')
      // 删除 userData
      this.$cookie.delete('userData')
      this.userData = ''
      this.isLogin = false
      this.$router.push('/refreshHome')
    }
  },
  mounted () {
    window.addEventListener('scroll', this.scrollToTop)
  },
  // 创建时检测本地是否有 cookie ,如果存在则请求用户信息并设置为已登录
  created () {
    const that = this
    // 如果存在且未过期
    if (that.$cookie.get('token') !== null) {
      // 请求用户信息并设置为已登录
      that.$http.get('/i', {
        params: {
          Cookie: that.$cookie.get('token')
        }
      })
        .then(function (response) {
          if (response.data.errorCode === 200) {
            // 将请求到的数据写到用户信息中
            that.userData = response.data.data
            // 设置为已登录
            that.isLogin = true
            // 用户信息写入 cookie
            that.$cookie.set('userData', response.data.data, {expires: '1h'})
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })
    }
  },
  components: {Login, About, Home}
}
</script>

<style>
    a {
        text-decoration: none;
    }

    a:link {
        text-decoration: none; /* 指正常的未被访问过的链接*/

    }

    a:visited {
        text-decoration: none; /*指已经访问过的链接*/

    }

    a:hover {
        text-decoration: none; /*指鼠标在链接*/
    }

    a:active {
        text-decoration: none; /* 指正在点的链接*/

    }
</style>



