<template>
  <div>
    <el-container style="text-align: center;position:relative;margin: 0; padding: 0;border: 0;">
      <!--背景图片-->
      <el-row>
        <el-col :span="24" :push="0" :class="`${searchResult?'animated fadeOutUp slow':''}`">
          <el-image style="width: 100%; height: 100%;opacity: 1"
                    src="../../static/img/homeImg.jpg"
                    fit="fill"/>
        </el-col>
      </el-row>
      <!--搜索框-->
      <!--                  :class="searchResult?'input-with-select searchResultInput':'input-with-select searchInput'"-->
      <el-row style="width: 100%;position: absolute;">
        <el-input placeholder="" v-model="inputText"
                  @focus="gotoSZ"
                  @keyup.enter.native="searchResults"
                  :class="`input-with-select ${searchResult?'searchResultInput':'searchInput'}`"
                  style="width: 50%;height: 55px;"
                  type="text"
                  :clearable="true"
                  size="large">

          <el-select v-model="RentalOptions.selectCityName.cityCnName" slot="prepend" placeholder="深圳市"
                     popper-class="selectOptions"
                     filterable
                     @change="changeCity">
            <el-option
              v-for="cityName in cityNames"
              :key="cityName.cityId"
              :value="cityName.cityPyName"
              :label="cityName.cityCnName">
              <span style="float: left">{{ cityName.cityCnName }}</span>
              <span style="float: right; color: #8492a6; font-size: 15px">{{ cityName.cityPyName }}</span></el-option>
          </el-select>
          <el-button-group slot="append">
<!--            <el-button icon="el-icon-location-information" @click="$router.push('/map')">地图找房</el-button>-->
<!--            <el-button icon="el-icon-search" @click="searchResults"/>-->
            <el-button icon="el-icon-search" @click="searchResults"/>
          </el-button-group>
        </el-input>
      </el-row>
      <!--筛选选项-->
      <el-row v-if="searchResult"
              style="position: absolute;width: 100%;margin-top: 7%;"
              class="animated fadeInUp slow el-container">
        <el-col :span="7" :push="5">
          <el-cascader-panel @change="changeCityRegion" :options="RentalOptions.areaOptions"
                             :props="RentalOptions.props"></el-cascader-panel>
        </el-col>

        <el-col :span="7" :push="5">
          <div style="border: 1px #E9EEF3 solid;width: 100%;height: 100%;">
            <el-container style="width: 100%;height: 100%;" direction="vertical">
              <!--类型-->
              <el-row style="margin-top: 18px">
                <span class="el-col-3" style="">类型</span>
                <span class="el-col-2 el-col-push-1" style="text-align: right">整租</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.typeOfRental.Z1"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
                <span class="el-col-2 el-col-push-1" style="text-align: right">合租</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.typeOfRental.Z2"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
              </el-row>

              <!--朝向-->
              <el-row style="margin-top: 18px">
                <span class="el-col-3" style="">朝向</span>
                <span class="el-col-2 el-col-push-1" style="text-align: right">东</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.directionOfHOuse.East"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
                <span class="el-col-2 el-col-push-1" style="text-align: right">南</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.directionOfHOuse.South"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
                <span class="el-col-2 el-col-push-1" style="text-align: right">西</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.directionOfHOuse.West"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
                <span class="el-col-2 el-col-push-1" style="text-align: right">北</span>
                <div class="el-col-3 el-col-push-1">
                  <el-switch
                    v-model="RentalOptions.directionOfHOuse.North"
                    active-color="#409eff"
                    inactive-color="#E9EEF3">
                  </el-switch>
                </div>
              </el-row>

              <!--价格区间-->
              <el-row style="margin-top: 18px">
                <span class="el-col-4" style="">价格区间</span>
                <el-col :span="19" :push="1">
                  <div style="width: 95%">
                    <el-slider
                      v-model="RentalOptions.priceRange"
                      :show-tooltip="false"
                      :marks="priceMark"
                      range
                      show-stops
                      :max="100">
                    </el-slider>
                  </div>
                </el-col>
              </el-row>
            </el-container>
          </div>
        </el-col>
      </el-row>
      <!--搜索结果-->
      <el-row v-if="searchResult"
              style="position: absolute;z-index: 0;margin-top: 18%" class="animated fadeInUp slow">
        <div style="display:flex;flex-direction: column;">
          <!--<p style="font-family: DengXian_Reg,serif;font-weight: bold;font-size: xx-large;text-align: center">推荐房源</p>-->
          <!--根据总数据长度决定有多少行-->
          <el-row type="flex" justify="center" class="row-bg" v-for="(item) in parseInt(zufangDataLength/3)+1"
                  :key="item"
                  align="middle"
                  style="margin-top: 2%">
            <!--每行三个数据-->
            <el-col v-if="index <= zufangDataLength" :span="5" v-for="(item, index) in 3" :key="item"
                    style="margin-left: 2%;margin-right: 2%">
              <el-card :body-style="{ padding: '0px' }">
                <img :src="zufangData[index].pictureUrl" class="image hvr-grow" alt="">
                <div style="padding-top:14px; padding-bottom: 14px; width: 100%;text-align: center;"
                     class="hvr-sweep-to-top">
                  <span>{{zufangData[index].name}}</span>
                  <div class="bottom clearfix">
                    <time class="time">{{zufangData[index].price}}<br/></time>
                  </div>
                </div>
              </el-card>
              <!---->
            </el-col>
          </el-row>
        </div>
      </el-row>
    </el-container>

    <Footer :class="searchResult?'animated fadeOutUp slow':''"/>
  </div>
</template>

<script>
import Header from './Header'
import Footer from './Footer'
import Axios from 'axios'

export default {
  name: 'home',
  data: function () {
    return {
      activeIndex: 1,
      inputText: '',
      cityNames: [
        // {'cityCnName': '深圳市', 'cityId': '4', 'cityPyName': 'sz'}
      ],
      zufangData: [
        {
          id: '001',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        },
        {
          id: '002',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        },
        {
          id: '003',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        },
        {
          id: '004',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        },
        {
          id: '005',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        },
        {
          id: '006',
          name: '房屋名称',
          rentalType: '房屋出租方式',
          orientation: '房屋朝向',
          area: '房屋面积',
          price: '￥800/月',
          pictureUrl: '../../static/img/test_1.jpg'
        }
      ],
      zufangDataLength: 0,
      // currentDate: new Date(),
      // 用户点击搜索后,将此项变为 true,从而激发动画
      searchResult: false,
      priceMark: {
        0: '<1000元',
        15: '1000元',
        29: '1500元',
        43: '2000元',
        57: '3000元',
        71: '5000元',
        85: '8000元',
        100: '>8000元'
      },
      // 租房选项
      RentalOptions: {
        // 用户选中的城市
        selectCityName: {
          cityCnName: '',
          cityId: '',
          cityPyName: ''
        },
        // 用户选择城市对应的区域
        // cityRegions: [],
        // 当前选择的区域
        // regionSelected: [],
        props: {
          lazy: true,
          lazyLoad (node, resolve) {
            const nodes = []
            if (node.path[0].split('_')[3] === '区域' && node.path.length === 1) {
              // console.log('请求城市区域开始')
              /*
               * 请求对应城市的区域;
               */
              const urlRegion = '/region/' + encodeURIComponent(node.path[0].split('_')[0])
              Axios.get(urlRegion, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].regionId,
                        label: response.data.data[i].regionCnName,
                        // 是否叶子结点
                        leaf: true
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            } else if (node.path[0].split('_')[3] === '地铁' && node.path.length === 1) {
              // console.log('请求城市地铁开始')
              /*
               * 请求对应城市的地铁;
               */
              const urlSubway = '/subwayline/' + node.path[0].split('_')[2]
              Axios.get(urlSubway, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].lineId,
                        label: response.data.data[i].lineCnName,
                        // 是否叶子结点
                        leaf: false
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            } else if (node.path[0].split('_')[3] === '地铁' && node.path.length === 2) {
              /*
               * 请求对应地铁线路的地铁站台;
               */
              const urlSubwayStation = '/subwaystation/' + node.path[1]
              Axios.get(urlSubwayStation, {
                params: {},
                headers: {
                  'Content-Type': 'application/json'
                }
              })
                .then(function (response) {
                  if (response.data.errorCode === 200) {
                    for (let i = 0; i < response.data.data.length; i++) {
                      nodes.push({
                        value: response.data.data[i].stationId,
                        label: response.data.data[i].stationCnName,
                        // 是否叶子结点
                        leaf: true
                        // leaf: level >= 1
                      })
                    }
                  }
                })
                .catch(function (error) {
                  console.log(error)
                })
                .then(function () {
                  // always executed
                  resolve(nodes)
                })
            }
          }
        },
        areaOptions: [
          {
            value: '',
            label: '区域',
            children: []
          },
          {
            value: '',
            label: '地铁',
            children: []
          }
        ],
        // 价格区间
        priceRange: [0, 100],
        // 租赁类型
        typeOfRental: {
          // 整租
          Z1: true,
          // 合租
          Z2: true
        },
        directionOfHOuse: {
          East: true,
          South: true,
          West: true,
          North: true
        }
      }
    }
  },
  components: {
    Footer, Header
  },
  watch: {},
  methods: {
    // 跳转到深圳
    gotoSZ () {
      this.$router.push('/zufang/sz')
    },
    changeCity (cityName) {
      const that = this
      for (let city in that.cityNames) {
        // 先检查是否有此属性
        if (that.cityNames.hasOwnProperty(city)) {
          // 在城市列表中找到用户选择的城市,并将其他信息(拼音等)补全
          if (that.cityNames[city].cityPyName === cityName) {
            that.RentalOptions.selectCityName.cityCnName = that.cityNames[city].cityCnName
            that.RentalOptions.selectCityName.cityPyName = that.cityNames[city].cityPyName
            that.RentalOptions.selectCityName.cityId = that.cityNames[city].cityId
          }
        }
      }
      // console.log(that.RentalOptions.selectCityName)
      that.RentalOptions.areaOptions[0].value = `${that.RentalOptions.selectCityName.cityCnName}_${that.RentalOptions.selectCityName.cityPyName}_${that.RentalOptions.selectCityName.cityId}_区域`
      that.RentalOptions.areaOptions[0].value = `${that.RentalOptions.selectCityName.cityCnName}_${that.RentalOptions.selectCityName.cityPyName}_${that.RentalOptions.selectCityName.cityId}_地铁`
      this.searchResults()
    },
    changeCityRegion (cityRegion) {
      console.log(cityRegion)
      // that.$http.post('/place', {
      //   regionCnName: cityRegion
      // }, {headers: {'Content-Type': 'application/json'}})
      //   .then(function (response) {
      //     // console.log(response)
      //     if (response.data.errorCode === 200) {
      //
      //     } else {
      //
      //     }
      //   })
      //   .catch(function (error) {
      //   })
    },
    // 用户点击搜索按钮(或者搜索框中回车/或者用户选择城市)
    searchResults () {
      const that = this
      // 判断当前选择的城市,跳转到不同的路径
      switch (that.RentalOptions.selectCityName.cityCnName) {
        case '深圳市':
          that.$router.push('/zufang/sz')
          break
        default:
          that.$router.push('/zufang/sz')
          that.RentalOptions.areaOptions[0].value = '深圳市_sz_4_区域'
          that.RentalOptions.areaOptions[1].value = '深圳市_sz_4_地铁'
          that.RentalOptions.selectCityName = JSON.parse('' +
              '{"cityCnName": "深圳市",' +
              '"cityId": "4",' +
              '"cityPyName": "sz"}')
          break
      }

      /*
        * 请求对应城市的房源;
        * /zufang/{city}/p/{param}/_search/{search}
        */
      that.$http.get('/zufang/' + that.RentalOptions.selectCityName.cityPyName, {
        params: {},
        headers: {
          'Content-Type': 'application/json'
        }
      })
        .then(function (response) {
          if (response.data.errorCode === 200) {
            console.log(response)
          }
        })
        .catch(function (error) {
          console.log(error)
        })
        .then(function () {
          // always executed
        })

        // 读取用户输入及其选择,并请求对应房源

        // 如果动画还没被激发过,激发动画
      while (!that.searchResult) {
        // 将 searchResult 的值变为 true ,这将激发动画
        that.searchResult = true

        // const backgroundImg = document.querySelector('.backgroundImg')
        // backgroundImg.classList.add('animated', 'fadeOutUp', 'slow')
      }
    }
  },
  created () {
    const that = this
    that.zufangDataLength = that.zufangData.length
    // 请求有房源的城市列表
    that.$http.get('/citys', {
      params: {},
      headers: {
        'Content-Type': 'application/json'
      }
    })
      .then(function (response) {
        if (response.data.errorCode === 200) {
          // [{"cityCnName": "城市中文名称","cityId": "城市id"}]
          that.cityNames = response.data.data
        }
      })
      .catch(function (error) {
        console.log(error)
      })
      .then(function () {
        // always executed
      })

      // const cityName = ''
      // 判断当前城市,如果已经选择城市,则激发动画,并请求对应城市的房源
      // if (this.$route.path !== '/') {
      //   //激发动画
      //
      //   //请求数据
      //   that.$http.get('/zufang', {
      //     params: {}
      //   })
      //     .then(function (response) {
      //       if (200 === response.data.errorCode) {
      //         // [{"id": "房屋标识id", "name": "房屋名称", "rentalType": "房屋出租方式", "orientation": "房屋朝向", "area": "房屋面积", "price": "房屋价格", "pictureUrl": "房屋展示图片url"}]
      //         //将获取到的数据写入租房数据中并渲染
      //         that.zufangData = response.data
      //         that.zufangDataLength = response.data.length
      //       }
      //     })
      //     .catch(function (error) {
      //       console.log(error)
      //     })
      //     .then(function () {
      //       // always executed
      //     })
      // }
  },
  filters: {
    // 格式化时间
    timeFormatter (e) {
      const year = e.getFullYear()
      const month = (e.getMonth() + 1).toString().padStart(2, '0')
      const day = e.getDate().toString().padStart(2, '0')
      return `${year}-${month}-${day}`
    }
  }
}
</script>
<style>
  /*去除 a 标签的下划线*/
  a {
    text-decoration: none;
  }

  a:link {
    text-decoration: none; /* 指正常的未被访问过的链接*/
  }

  a:visited {
    text-decoration: none; /*指已经访问过的链接*/
  }

  a:hover {
    text-decoration: none; /*指鼠标在链接*/
  }

  a:active {
    text-decoration: none; /* 指正在点的链接*/
  }

  .header {
    position: relative;
    width: 100%;
    z-index: 999;
  }

  .header-active {
    /*background-color: rgba(255, 255, 255, 1);*/
    position: fixed;
    top: 0;
    width: 100%;
    z-index: 999;
  }

  .el-footer {
    /*color: #333;*/
    text-align: center;
    line-height: 60px;
  }

  .el-main {
    background-color: #E9EEF3;
    color: #333;
    text-align: center;
    /*line-height: 3000px;*/
  }

  body > .el-container {
    margin-bottom: 40px;
  }

  /*搜索框左边选择模块的大小*/
  .input-with-select .el-input-group__prepend {
    background-color: #fff;
    width: 12%;
    font-size: smaller;
    text-align: left;
    font-family: DengXian_Reg, serif;
  }

  /*搜索框左边选择模块展开的页面宽度*/
  .selectOptions {
    width: 5%;
  }

  /*.time {*/
  /*  font-size: 13px;*/
  /*  !*color: #999;*!*/
  /*  color: red;*/
  /*}*/

  .bottom {
    margin-top: 13px;
    line-height: 12px;
  }

  .image {
    width: 100%;
    display: block;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }

  .clearfix:after {
    clear: both
  }

  .el-select {
    font-size: 16px;
    height: 55px;
  }

  .el-input__inner {
    /*border: 1px solid #dcdfe6;*/
    /*width: 300px;*/
    height: 55px !important;
  }

  /*右侧按钮的宽度*/
  .el-input-group__append {
    width: 10%;
  }

  .searchInput {
    margin-top: 10%;
    margin-left: 25%;
    margin-right: 25%;
    z-index: 990;
  }

  .searchResultInput {
    width: 50%;
    margin-top: 10%;
    margin-left: 25%;
    margin-right: 25%;

    height: 55px;
    z-index: 990;

    animation: moveSearchInput 1s;
    -moz-animation: moveSearchInput 1s;
    -webkit-animation: moveSearchInput 1s;
    -o-animation: moveSearchInput 1s;

    animation-fill-mode: forwards;
    /* Safari 和 Chrome */
    -webkit-animation-fill-mode: forwards;
    -moz-animation-fill-mode: forwards;
    -o-animation-fill-mode: forwards;
  }

  @keyframes moveSearchInput {
    0% {
      margin-top: 10%;
      /*margin-left: -24.5%;*/
      margin-left: 25%;
    }
    100% {
      margin-top: 2%;
      /*margin-left: -32%;*/
      margin-left: 25%;
    }
  }

  /* Firefox 和 Chrome */
  @-webkit-keyframes moveSearchInput {
    0% {
      margin-top: 10%;
      /*margin-left: 24.5%;*/
      margin-left: 25%;
    }
    100% {
      margin-top: 2%;
      /*margin-left: -32%;*/
      margin-left: 25%;
    }
  }

  body {
    z-index: 0;
    overflow: scroll;
  }

  /*筛选区域下划线的宽度*/
  .el-divider {
    margin: 1% 0;
  }
</style>



